.radio {
  .state-layer {
    .container {
      @apply border-2 rounded-1/2;
      @apply w-sm+4 h-sm+4;
    }
  }

 &.disabled {
  &.checked {
    .container {
      @apply bg-transparent;
    }
  }
 }

 &:not(.disabled) {
  &.primary, &.secondary, &.tertiary, &.error, &.success {
    &.checked {
      .state-layer {
        .container {
          @apply bg-transparent;
        }
      }
    }
  }
 }
}
