.badge {
  @apply font-inter font-semibold text-caption-sm whitespace-nowrap;
  @apply inline-flex items-center justify-center;
  @apply rounded-1/2;
  @apply w-none+6;
  @apply h-none+6;

  &.sm {
    @apply w-sm+2;
    @apply h-sm+2;
  }

  &.lg {
    @apply w-auto h-auto;
    @apply rounded-100px;
    @apply px-xs;
    @apply py-none+1;
  }

  &.primary {
    @apply bg-primary;
    @apply text-on-primary;
  }

  &.secondary {
    @apply bg-secondary;
    @apply text-on-secondary;
  }

  &.tertiary {
    @apply bg-tertiary;
    @apply text-on-tertiary;
  }

  &.error {
    @apply bg-error;
    @apply text-on-error;
  }

  &.success {
    @apply bg-success;
    @apply text-on-success;
  }
}
