.button {
  @apply font-medium font-inter;
  @apply rounded;
  @apply overflow-hidden;
  @apply flex items-stretch;
  -webkit-tap-highlight-color: transparent;

  .state-layer {
    @apply flex;
    .content {
      @apply flex items-center;
      svg {
        @apply w-md h-md;
      }
      .icon-left {
        @apply -ml-xs mr-none+3;
      }
      .icon-right {
        @apply ml-none+3 -mr-xs;
      }
    }
  }

  &.flexible {
    .state-layer {
      @apply w-100% justify-center;
    }
  }

  &.lg:not(.flexible) {
    .state-layer {
      @apply px-md+4;
    }
  }

  &.lg {
    @apply h-2xl+4;
    @apply text-button-lg;
  }

  &.sm:not(.flexible) {
    .state-layer {
      @apply px-md;
    }
  }

  &.sm {
    @apply h-xl;
    @apply text-button-sm;
  }

  &.outlined {
    &:not(:disabled) {
      &:hover {
        @apply border-2;
        &.lg:not(.flexible) {
          .state-layer {
            @apply px-md+3;
          }
        }
        &.sm:not(.flexible) {
          .state-layer {
            @apply px-sm+7;
          }
        }
      }
    }
  }

}
