.suggestion-chip {
  @apply font-inter font-semibold text-2xs whitespace-nowrap;
  @apply inline-flex items-center justify-center;
  @apply rounded-6px;
  @apply h-lg min-h-lg min-w-5xl+5 px-none+7;

  &.low {
    @apply bg-secondary-container;
    @apply text-on-surface;
  }

  &.low-alternative {
    @apply bg-surface;
    @apply text-on-surface;
  }

  &.high {
    @apply bg-secondary;
    @apply text-secondary-container;
  }

  &.high-alternative {
    @apply bg-on-surface;
    @apply text-surface;
  }
}
