.avatar {
  @apply font-semibold font-inter;
  @apply bg-surface;
  @apply overflow-hidden;
  @apply flex items-center justify-center;

  img {
    @apply w-100% h-100% object-cover;
  }

  .default-icon {
    @apply fill-outline-variant;
  }

  &.circle {
    @apply rounded-1/2;
  }

  &.square {
    @apply rounded;
    &.xxs {
      @apply rounded-4px;
    }
  
    &.xs {
      @apply rounded-4px;
    }
  
    &.sm {
      @apply rounded-6px;
    }
  
    &.md {
      @apply rounded-6px;
    }
  
    &.lg {
      @apply rounded-8px;
    }
  
    &.xl {
      @apply rounded-8px;
    }
  }

  &.xxs {
    @apply w-md h-md min-w-md min-h-md;
    @apply text-caption-sm;
    .default-icon {
      @apply w-xs+6;
    }
  }

  &.xs {
    @apply w-lg h-lg min-w-lg min-h-lg;
    @apply text-caption-lg;
    .default-icon {
      @apply w-sm+1;
    }
  }

  &.sm {
    @apply w-xl h-xl min-w-xl min-h-xl;
    @apply text-body-sm;
    .default-icon {
      @apply w-sm+6;
    }
  }

  &.md {
    @apply w-2xl h-2xl min-w-2xl min-h-2xl;
    @apply text-body-lg;
    .default-icon {
      @apply w-md+4;
    }
  }

  &.lg {
    @apply w-3xl h-3xl min-w-3xl min-h-3xl;
    @apply text-body-lg;
    .default-icon {
      @apply w-md+7;
    }
  }

  &.xl {
    @apply w-4xl h-4xl min-w-4xl min-h-4xl;
    @apply text-body-lg;
    .default-icon {
      @apply w-lg+4;
    }
  }
}
